#bg-map{
    margin-left: 3vw;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 20px;
    border-radius: 20px;
    width: fit-content;
}

#index{
    padding-top: 50vh;
    padding-bottom: 30vh;
}

#Us {
    background-color: rgba(229, 229, 229, 0.40);
    box-shadow: 0px 8px 8px 10px rgba(0, 0, 0, 0.25);
    display: flex;
    align-content: center;
    justify-items: center;
    justify-content: space-evenly;
    align-items: center;
    padding-top: 14vh;
    width: 75vw;
    height: 100vh;
    position: absolute;
    inset: 0;
    margin: auto;
    z-index: 0;
}
.JrTeamdiv{
    position: absolute;
    top: 212vh;
    background-color: rgba(250, 250, 250, 0.548);
    padding: 3%;
    border-radius: 20px;
    border-style: dashed;
    border-color: #000000d5;
    border-width: 3px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media (max-width: 450px) {
    #indexInf{
        top: 40vh;
    }
    #index{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 100vw;
        font-size: 50px;
    }
    .JrTeam{
        width: 200vw;
        margin-top: 20vh;
    } 
    .JrTeamText{
        margin-top: 0;
    }
    .JrTeamheader{
        margin-bottom: 2vh;
    }
    .JrTeamdiv{
        width: 275vw;
        padding: 1%;
        background-color: #ffffff9d;
        position: sticky;
        border-radius: 20px;
        border-style: dashed;
        border-width: 10px;
        margin-top: 10vh;
        margin-left: 1vw;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    #indexInf{
        position: relative;
    }
    #IndexColorLogo{
        position: absolute;
        height: auto;
        width: 160vw;
        z-index: 2;
        top: -25vh;
        left: 60vw;
    }
    #jrText{
        width: 275vw;
        padding: 1.5%;
        background-color: #ffffff9d;
        position: sticky;
        border-radius: 20px;
        border-style: dashed;
        border-width: 10px;
        margin-top: 100vh;
        padding-bottom: 20vh;
        margin-left: 1vw;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .PorQueJRheader  {
        font-size: 70px;
        
      }
    #bg-map{
        
        margin-left: 3vw;
        margin-top: 75vh;
        background-color: rgba(0, 0, 0, 0.719);
        padding: 20px;
        border-radius: 20px;
        width: fit-content;
        border-style: dashed;
        border-color: #ffffffd5;
        border-width: 4px;
      }
    #bg-map iframe{
        width: 1000px;
        height: 1300px;
      }
    #mapTxt{
        color: #ffff;
        font-size: 40px;
    }
    #Us {
        background-color: #ffffff00;
        box-shadow: none;
        flex-direction: column;
        align-items: stretch;
        width: 260vw;
        margin-left: 2vw;
    }
}