body {
    background-color: rgba(53, 53, 53, 1);
    font-family: 'Eras ITC';
    overflow-x: hidden;
    overflow-y: auto;
  }

  .bg-images {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 75vw;
    margin: auto;
    overflow-x: hidden;
    overflow-y: auto;
    z-index: -1;
  }

#index {
    padding-top: 80vh;
    padding-bottom: 10vh;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    align-items: end;
    margin-left: auto;
    margin-right: auto;
    width: 55vw;
    height: 210vh;
}
#IndexColorLogo {
  margin-bottom: 10vh;
}
#jrText{
  background-color: rgba(250, 250, 250, 0.548);
  padding: 3%;
  padding-inline: 4%;
  border-radius: 20px;
  border-style: dashed;
  border-color: #000000d5;
  border-width: 3px;
}
.PorQueJr{
    row-gap: 40vh;
    grid-template-rows: auto auto;
    width: fit-content;
    height: fit-content;

}

.JrTeam{
    grid-column: span 2;
    width: 32vw;
}

#IndexColorLogo {
    width: 25vw;
    height: fit-content;
    align-items: center;
}

.no-scroll {
    overflow: hidden; /* Esto evitará que se haga scroll en esta sección específica */
  }

  @media (max-width: 450px) {
    .bg-images {
      width: 350vw;
      max-width: 1200px;
      overflow: hidden;
      filter: blur(3px);
    }
  }