#social {
    background-color: rgba(229, 229, 229, 0.40);
    box-shadow: 0px 8px 8px 10px rgba(0, 0, 0, 0.25);
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-content: center;
    justify-items: center;
    align-items: center;
    padding-top: 14vh;
    width: 75vw;
    height: 100vh;
    position: absolute;
    inset: 0;
    margin: auto;
    z-index: 0;
  }

#social img {
  margin: 2vw;
}

#bg-content{
    margin-top: 6%;
    background-color: rgba(0, 0, 0, 0.6);
    padding: 20px;
    padding-top: 2px;
    border-radius: 20px;
}

#bg-content h2 {
  font-size: 28px;
  font-weight: 1000;
  color: #fff;
}
@media (max-width: 450px) {
  #bg-content{
    display: flex;
    margin-left: 3vw;
    margin-top: 70vh;
    background-color: rgba(0, 0, 0, 0.719);
    padding: 2%;
    border-radius: 20px;
    width: fit-content;
    flex-direction: column;
    align-items: center;
    border-radius: 20px;    
    border-style: dashed;
    border-color: #ffffffd5;
    border-width: 4px;
  }
#bg-content iframe{
    width: 1000px;
    height: 1300px;
  }
#socialMedia{
    opacity: 0;
}
#socialTxt h2{
  font-size: 50px;
}
#social {
    display: flex;
    background-color: #ffffff00;
    box-shadow: none;
    flex-direction: column;
    align-items: stretch;
    width: 260vw;
    margin-left: 2vw;
}
}
